import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import * as _ from 'underscore';
import { GeneralService } from '../../../../../services/general.service';
import { HeaderService } from '../../../../../services/header.service';
import { AddExpenseComponent } from '../add-expense/add-expense.component';
import { ModalImageComponent } from '../modal-image/modal-image.component';
import { PettyCashService } from '../services/petty-cash.service';
import { SubjectService } from '../../../../../services/subject.service';
import { Subscription, forkJoin } from 'rxjs';
import { FormControl } from '@angular/forms';
import { ProvideExpensesService } from '../../../main-fund/provide-expenses/provide-expense-service/provide-expenses.service';
import { SharedService } from '../../../../../services/shared.service';

@Component({
  selector: 'app-expense',
  templateUrl: './expense.component.html',
  styleUrls: ['./expense.component.scss'],
})
export class ExpenseComponent implements OnInit {
  // @ViewChild('pager', { static: true }) pager!: PaginationComponent;

  expenses!: any[];
  totalRecords!: number;
  totalAmount: number;
  totalPages!: number;
  items = [1, 2, 1, 2, 1, 2];
  expenseId = new FormControl('');
  searchText: string = '';
  status: string = '';
  sort: number = 1;

  pageNo: number = 1;
  pagin!: number;
  pages!: number[];
  statusList: any;
  pettyExpenses;
  subscription: Subscription;
  sumOfExpenses = {
    pending: 0,
    approved: 0,
    notConfirmed: 0,
  }
  constructor(
    private pettyCashService: PettyCashService,
    public generalService: GeneralService,
    public _subjectService: SubjectService,
    public headerService: HeaderService,
    private modalService: NgbModal,
    private router: Router,
    config: NgbModalConfig,
    private _provideExpensesService: ProvideExpensesService,
    public _sharedService: SharedService,

  ) {
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnInit(): void {
    this.GetMainData();
    this.getStatus();
    this.getRegisterPettyCashSituation();
    this.subscription = this._subjectService.replenishmentRequestReceived.subscribe(x => {
      if (x) {
        this.getRegisterPettyCashSituation();
      }
    })
    this.expenseFilters();
    this.calculateExpenses();

  }
  calculateExpenses() {
    let regId = this._sharedService.getRegister?.registerObj?.id;
    let peParams = `?registerId=${regId}&pageSize=10000`;
    let pendingExpenses = this.pettyCashService.getRegisterPettycashrequestNew(peParams);
    let aeParams = `?pageSize=10000`;
    let approvedExpenses = this._provideExpensesService.getProvideExpenses(aeParams);
    forkJoin([pendingExpenses, approvedExpenses]).subscribe((response: any) => {
      this.sumOfExpenses.pending = response[0].data.filter(x => x.status == 2003).reduce((acc, cur) => {
        return acc += cur.requestedAmount
      }, 0);
      this.sumOfExpenses.approved = response[1].data.filter(x => !x.provided && x.register.registerId == regId).reduce((acc, cur) => {
        return acc += cur.approvedAmount
      }, 0);
      this.sumOfExpenses.notConfirmed = response[0].data.filter(x => x.provided && !x.confirmed && x.status == 2004).reduce((acc, cur) => {
        return acc += cur.approvedAmount
      }, 0);
    });
  }
  expenseFilters() {
    this.expenseId.valueChanges.subscribe(value => {
      this.pageNo = 1;
      this.GetMainData();
    })
  }
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
  Viewdetails(item) {
    this.router.navigateByUrl('/petty-cash/request-details/1');
    this.headerService.setTitle(
      'Petty Cash > Petty Cash Replenishment Request > View Details'
    );
  }
  viewExpense() {
    this.headerService.setTitle('petty-cash > View role');
  }
  getRegisterPettyCashSituation() {
    let registerId = this.generalService.sharedService.getRegister?.registerObj?.id
    this.pettyCashService.getRegisterPettyCashSituation(registerId).subscribe((response: any) => {
      console.log(response);
      this.pettyExpenses = response.data;
    })
  }
  GetMainData() {
    return this.pettyCashService
      .GetPettyCashExpenses(
        this.searchText,
        this.expenseId.value,
        this.sort,
        this.status,
        (this.pageNo - 1)
        // this.pager.pageSize
      )
      .subscribe((response: any) => {
        if (response) {
          this.expenses = response.data;
          this.totalRecords = response.info.totalRecordCount;
          this.totalAmount = response.info.totalAmount;
          //this.totalRecords = response.info.totalRecordsCount;
          this.pagin = Math.ceil(this.totalRecords / 6);
          this.pages = _.range(this.pagin);
          console.log(this.pagin);
          console.log(this.pages);
          // this.pager.EventsCount = this.totalRecords;
          // this.pager.setPage(this.pager.GlobalPageIndex, false);
          // console.log(this.pager.EventsCount);
          // console.log(this.pager.GlobalPageIndex);
          // console.log(this.pager);
        }
      });
  }
  searchTable(event: any) {
    console.log(event?.target.value);
    const text = event.target.value;
    console.log(text.length);
    if (text.length >= 2) {
      this.searchText = text;
      this.pageNo = 1;
      this.GetMainData();
    }
    if (text.length == 0) {
      this.GetMainData();
    }
  }
  removeSearch() {
    this.searchText = '';
    this.sort = 1;
    this.GetMainData();
  }

  sortByInvoice() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.GetMainData();
  }
  sortByCategory() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.GetMainData();
  }
  sortByIDate() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.GetMainData();
  }
  sortByTotalAmount() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.GetMainData();
  }

  sortByAmount() {
    if (this.sort == 10) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 9 ? 10 : 9;
    }
    this.GetMainData();
  }
  sortByStatus() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.GetMainData();
  }

  setPage(page: number) {
    this.pageNo = page;
    this.GetMainData();
  }
  pageChange(page) {
    this.pageNo = page;
    this.GetMainData();
    window.scroll(0, 0);
  }
  filterByStatus(event) {
    console.log(event);
    this.pageNo = 1;
    this.status = event.id;
    this.GetMainData();
  }
  clearStatus(event) {
    console.log(event);
    this.status = '';
    this.GetMainData();
  }

  getStatus() {
    //this.generalService.getLookupsStatus().subscribe((response: any) => {
    //  this.statusList = response.data;
    //});
    this.generalService.getLookupsById(`2&pageSize=100`).subscribe((response: any) => {
      this.statusList = response.data.filter(item => item.id == 2011 || item.id == 2012 || item.id == 2003 || item.id == 2004 || item.id == 2006);

    });
  }
  openModalExpense() {
    const modalRef = this.modalService.open(AddExpenseComponent, {
      size: 'lg',
    });
    modalRef.componentInstance.type = 'add';
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      // IN CASE OF EXPENSE STATUS IS REVISED
      if (result?.status != 2012)
        this.modalService.dismissAll();
      this.pageNo = 1;
      this.sort = 1;
      this.GetMainData();
      this.getRegisterPettyCashSituation();
    });
  }

  openModalExpenseView(item) {
    const modalRef = this.modalService.open(AddExpenseComponent, {
      size: 'lg',
    });
    modalRef.componentInstance.expenseId = item.expenseRecordId;
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      console.log(result);
      if (result.status != 2012)
        this.modalService.dismissAll();
      this.pageNo = 1;
      this.sort = 1;
      this.GetMainData();
      this.getRegisterPettyCashSituation();
    });
  }

  openMdalImage(item) {
    const modalRef = this.modalService.open(ModalImageComponent);
    modalRef.componentInstance.expenseDetails = item;
  }
}
