import { Component, OnInit, ViewChild } from '@angular/core';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { GeneralService } from '../../../../../services/general.service';
import { HeaderService } from '../../../../../services/header.service';
import { PettyCashService } from '../services/petty-cash.service';
import { SubjectService } from '../../../../../services/subject.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalMessageComponent } from '../../../../../shared/components/modal-message/modal-message.component';
import { SharedService } from '../../../../../services/shared.service';
import { AddExpenseV2Component } from '../add-expense-v2/add-expense-v2.component';
import { ConfirmModalComponent } from '../../../../../shared/components/confirm-modal/confirm-modal.component';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import { CommonService } from '../../../../../services/common.service';
import { ViewFileComponent } from '../../collect/payment-modal/view-file/view-file.component';
import { forkJoin } from 'rxjs';
import { ProvideExpensesService } from '../../../main-fund/provide-expenses/provide-expense-service/provide-expenses.service';
@Component({
  selector: 'petty-cash-payments',
  templateUrl: './petty-cash-payments.component.html',
  styleUrls: ['./petty-cash-payments.component.scss']
})
export class PettyCashPaymentsComponent {
  totalRecords: number;
  pageNo: number = 1;
  statusList: any;
  pettyExpenses;
  paymentForm: FormGroup;
  filterForm: FormGroup;
  filterParams;
  expenseCategoryList = [];
  employeeList = [];
  cashierEmployeeList = [];
  costCenterList = [];
  costCenterTypeList = [];
  pettyCashPaymentList = [];
  uploadedFile;
  isPending = false;
  sumOfExpenses = {
    pending: 0,
    approved: 0,
    notConfirmed: 0,
  }
  constructor(
    private fb: FormBuilder,
    private pettyCashService: PettyCashService,
    private _provideExpensesService: ProvideExpensesService,
    public _sharedService: SharedService,
    public generalService: GeneralService,
    public _subjectService: SubjectService,
    public headerService: HeaderService,
    private modalService: NgbModal,
    private _datePipe: DatePipe,
    private _commonService: CommonService,
    config: NgbModalConfig
  ) {
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnInit(): void {
    this.initForm();
    this.initFilterForm();
    //this.getStatus();
    this.getEmployees();
    this.getCashierEmployees();
    this.getExpenseCategory();
    this.getRegisterPettyCashSituation();
    this.getPettyCashPayments();
    this.detectFilterFormChanges();
    this.getCostCenterType();
    this.calculateExpenses();
  }
  initForm() {
    this.paymentForm = this.fb.group({
      amount: ['', [Validators.required]],
      givenToName: ['', [Validators.required]],
      categoryId: [null, [Validators.required]],
      costCenterType: [null, [Validators.required]],
      costCenterId: [null, [Validators.required]],
      approverName: [{ value: '', disabled: true }],
      ide: [false],
      expenseTax: ['0'],
      expenseTotalAmount: ['', [Validators.required]],
      invoiceDate: [null],
      invoiceNo: [''],
      notes: [''],
      attachment: [''],
    })
    this.paymentForm.get('ide').valueChanges.subscribe(value => {
      if (value) {
        this.paymentForm.get('expenseTax').setValidators([Validators.required])
        this.paymentForm.get('expenseTax').updateValueAndValidity();
        this.paymentForm.get('expenseTotalAmount').setValidators([Validators.required])
        this.paymentForm.get('expenseTotalAmount').updateValueAndValidity();
        this.paymentForm.get('invoiceNo').setValidators([Validators.required])
        this.paymentForm.get('invoiceNo').updateValueAndValidity();
        this.paymentForm.get('invoiceDate').setValidators([Validators.required])
        this.paymentForm.get('invoiceDate').updateValueAndValidity();
        this.paymentForm.get('attachment').setValidators([Validators.required])
        this.paymentForm.get('attachment').updateValueAndValidity();
      }
      else {
        this.paymentForm.get('expenseTax').clearValidators();
        this.paymentForm.get('expenseTax').updateValueAndValidity();
        this.paymentForm.get('expenseTotalAmount').clearValidators();
        this.paymentForm.get('expenseTotalAmount').updateValueAndValidity();
        this.paymentForm.get('invoiceNo').clearValidators();
        this.paymentForm.get('invoiceNo').updateValueAndValidity();
        this.paymentForm.get('invoiceDate').clearValidators();
        this.paymentForm.get('invoiceDate').updateValueAndValidity();
        this.paymentForm.get('attachment').clearValidators();
        this.paymentForm.get('attachment').updateValueAndValidity();
      }
    })
  }
  calculateExpenses() {
    let regId = this._sharedService.getRegister?.registerObj?.id;
    let peParams = `?registerId=${regId}&pageSize=10000`;
    let pendingExpenses = this.pettyCashService.getRegisterPettycashrequestNew(peParams);
    let aeParams = `?pageSize=10000`;
    let approvedExpenses = this._provideExpensesService.getProvideExpenses(aeParams);
    forkJoin([pendingExpenses, approvedExpenses]).subscribe((response: any) => {
      this.sumOfExpenses.pending = response[0].data.filter(x => x.status == 2003).reduce((acc, cur) => {
        return acc += cur.requestedAmount
      }, 0);
      this.sumOfExpenses.approved = response[1].data.filter(x => !x.provided && x.register.registerId == regId).reduce((acc, cur) => {
        return acc += cur.approvedAmount
      }, 0);
      this.sumOfExpenses.notConfirmed = response[0].data.filter(x => x.provided && !x.confirmed && x.status == 2004).reduce((acc, cur) => {
        return acc += cur.approvedAmount
      }, 0);
    });
  }
  costCenterChange(event) {
    this.paymentForm.get('approverName').setValue(event?.approverUser?.fullName)
  }
  getCostsCenter(id) {
    this.pettyCashService.getCostCentersByTypeId(id).subscribe((response: any) => {
      this.costCenterList = response.data;
    })
  }
  getCostCenterType() {
    this.generalService.getLookupsById(38).subscribe((response: any) => {
      this.costCenterTypeList = response.data.map(x => {
        return {
          ...x,
          text: x?.name[0]?.lookupName
        }
      });
    })
  }
  handleCostCenterTypeChange(event) {
    this.paymentForm.get('costCenterId').setValue(null);
    this.paymentForm.get('approverName').setValue('');
    this.costCenterList = [];
    if (event) {
      this.getCostsCenter(event.id);
    }
  }
  openPettyCashPaymentModal(content) {
    const modelRef = this.modalService.open(content, { size: 'lg' });
    modelRef.result.then((x) => {
      if (x === 'close') {
        this.paymentForm.reset(this.initForm());
        this.costCenterList = [];
      }
    }, error => { })
  }
  handleFocusOut(control) {
    let value = +this.paymentForm.controls[control].value;
    if (value > 0) {
      this.paymentForm.controls[control].setValue(value.toFixed(3));
      //let tax = ((+this.paymentForm.value['amount']) / 100) * (+this.paymentForm.value['expenseTax']);
      //this.paymentForm.controls['expenseTotalAmount'].setValue((tax + (+this.paymentForm.value['amount'])).toFixed(3));
      let tax = (((+this.paymentForm.value['expenseTax']) / 100) + 1) * (+this.paymentForm.value['amount']);
      this.paymentForm.controls['expenseTotalAmount'].setValue(tax.toFixed(3));
      this.paymentForm.controls[control].setErrors(null);
    }
  }
  handleTotalAmountChange() {
    let value = +this.paymentForm.get('expenseTotalAmount').value;
    if (value > 0) {
      let amount = (+this.paymentForm.value['expenseTotalAmount']) / (((+this.paymentForm.value['expenseTax']) / 100) + 1);
      this.paymentForm.controls['amount'].setValue(amount.toFixed(3));
      //  this.paymentForm.get('expenseTotalAmount').setValue(value.toFixed(3));
      //  let tax = ((+this.paymentForm.value['expenseTotalAmount']) / 100) * (+this.paymentForm.value['expenseTax']);
      //  this.paymentForm.controls['amount'].setValue(((+this.paymentForm.value['expenseTotalAmount']) - tax).toFixed(3));
    }
  }
  addPettyCashPayment() {
    if (this.paymentForm.invalid) {
      this.paymentForm.markAllAsTouched();
      return;
    }
    this.isPending = true;
    let formData = new FormData();
    let values = this.paymentForm.value;
    formData.append('categoryId', values?.categoryId);
    formData.append('costCenterId', values?.costCenterId);
    formData.append('givenToName', values?.givenToName);
    formData.append('withExpense', values?.ide);
    formData.append('amount', values?.amount);
    if (values?.ide) {
      formData.append('expenseTax', values?.expenseTax);
      formData.append('totalAmount', this.paymentForm.get('expenseTotalAmount').getRawValue());
      formData.append('invoiceNo', values?.invoiceNo);
      let invoiceDate = this._datePipe.transform(values?.invoiceDate, 'yyyy-MM-dd')
      formData.append('invoiceDate', invoiceDate);
      formData.append('attachments', this.uploadedFile?.file);
    }
    formData.append('notes', values?.notes);
    this.pettyCashService.givePettyCashPayment(formData).subscribe(response => {
      if (response.isSuccess) {
        this.getPettyCashPayments();
        this.getRegisterPettyCashSituation();
        this.paymentForm.reset(this.initForm());
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'success';
        modalRef.componentInstance.message = 'Payment Successfully Added';
      }
    }, error => {
      const modalRef = this.modalService.open(ModalMessageComponent);
      modalRef.componentInstance.type = 'error';
      modalRef.componentInstance.messageError = error?.error?.errors;
    }).add(() => this.isPending = false)
  }
  approvePayment(data) {
    const modalRef = this.modalService.open(AddExpenseV2Component, { size: 'lg' });
    modalRef.componentInstance.paymentDetails = data;
    modalRef.componentInstance.eventData.subscribe(x => {
      if (x) {
        this.getPettyCashPayments();
        this.getRegisterPettyCashSituation();
      }
    });
  }
  cancelPayment(data) {
    const modalRef = this.modalService.open(ConfirmModalComponent, { size: 'md' });
    let body = { body: 'Are you sure you want to cancel this petty cash payment?' };
    modalRef.componentInstance.data = body;
    modalRef.componentInstance.eventData.subscribe(x => {
      if (x) {
        let formData = new FormData();
        formData.append('PettyCashPaymentId', data?.pettyCashPaymentId);
        this.pettyCashService.cancelGivenPettyCashPayment(formData).subscribe(response => {
          if (response.isSuccess) {
            this.getPettyCashPayments();
            this.getRegisterPettyCashSituation();
            const modalRef = this.modalService.open(ModalMessageComponent);
            modalRef.componentInstance.type = 'success';
            modalRef.componentInstance.message = 'Succesfully done!';
          }
        }, error => {
          const modalRef = this.modalService.open(ModalMessageComponent);
          modalRef.componentInstance.type = 'error';
          modalRef.componentInstance.messageError = error?.error?.errors;
        })
      }
    });
  }
  fileAction(action) {
    if (action == 'view') {
      const modelRef = this.modalService.open(ViewFileComponent, {
        size: 'md',
      })
      modelRef.componentInstance.imageUploadedView = this.uploadedFile;
    }
    else {
      (document.getElementById('file-input') as HTMLInputElement).value = null;
      this.paymentForm.get('attachment')?.setValue('');
      this.uploadedFile = null;
    }
  }
  onFileChange(event) {
    let files = [...event.target.files];
    if (files.length > 0) {
      let isInvalid = this._commonService.checkInvalidImageFormat(files);
      if (!isInvalid) {
        files.forEach((file: File) => {
          this._commonService.fileToBase64(file).then(response => {
            this.uploadedFile = response;
            this.paymentForm.get('attachment')?.setValue('Attachment');
          })
        })
      }
    }
  }
  getExpenseCategory() {
    this.pettyCashService.getLookupsById(12).subscribe((response: any) => {
      this.expenseCategoryList = response.data.map((x) => {
        return {
          id: x.id,
          name: x.name[0].lookupName
        }
      });
    });
  }

  getEmployees() {
    this.pettyCashService.getEmployees().subscribe((response: any) => {
      this.employeeList = response.data;
    })
  }
  getCashierEmployees() {
    this.pettyCashService.getCashierEmployees().subscribe((response: any) => {
      this.cashierEmployeeList = response.data;
    })
  }
  getPettyCashPayments(filterParams = this.filterParams) {
    let defaultParams = `registerId=${this._sharedService.getRegister.registerObj.id}&pageNo=${this.pageNo - 1}&pageSize=6`
    this.pettyCashService.getPettyCashPayments((filterParams && filterParams + `&${defaultParams}`) || (`?${defaultParams}`)).subscribe((response: any) => {
      this.pettyCashPaymentList = response.data;
      this.totalRecords = response.info.totalRecordsCount;
    })
  }
  detectFilterFormChanges() {
    this.filterForm.valueChanges.subscribe(value => {
      this.pageNo = 1;
      let formValues = value;
      if (formValues.status == null) {
        formValues = { ...formValues, status: '' }
      }
      //if (formValues.givenTo == null) {
      //  formValues = { ...formValues, givenTo: '' }
      //}
      if (formValues.createdBy == null) {
        formValues = { ...formValues, createdBy: '' }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      this.getPettyCashPayments(this.filterParams)
    })
  }
  initFilterForm() {
    this.filterForm = this.fb.group({
      status: [null],
      givenToName: [''],
      createdBy: [null],
    })
  }
  getRegisterPettyCashSituation() {
    let registerId = this.generalService.sharedService.getRegister?.registerObj?.id
    this.pettyCashService.getRegisterPettyCashSituation(registerId).subscribe((response: any) => {
      console.log(response);
      this.pettyExpenses = response.data;
    })
  }
  pageChange(page: number) {
    this.pageNo = page;
    this.getPettyCashPayments();
  }
  getStatus() {
    this.generalService.getLookupsStatus().subscribe((response: any) => {
      this.statusList = response.data;
    });
  }
  calculateDaysBetweenDates(startDate) {
    const startMoment = moment(this._datePipe.transform(startDate, 'yyyy-MM-dd'));
    const currentDate = moment(this._datePipe.transform(new Date(), 'yyyy-MM-dd'));
    const numberOfdays = currentDate.diff(startMoment, 'days');
    return numberOfdays;
  }
}
