import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import * as _ from 'underscore';
import { HeaderService } from '../../../../../services/header.service';
import { SharedService } from '../../../../../services/shared.service';
import { AddPettyCashComponent } from '../add-petty-cash/add-petty-cash.component';
import { ModalImageComponent } from '../modal-image/modal-image.component';
import { PettyCashService } from '../services/petty-cash.service';
import { AddRequestComponent } from './add-request/add-request.component';
import { SubjectService } from '../../../../../services/subject.service';
import { ViewFileComponent } from '../../collect/payment-modal/view-file/view-file.component';
import { CommonService } from '../../../../../services/common.service';
@Component({
  selector: 'app-requests',
  templateUrl: './requests.component.html',
  styleUrls: ['./requests.component.scss'],
})
export class RequestsComponent implements OnInit {
  expenses!: any[];
  totalRecords!: number;
  totalPages!: number;
  items = [1, 2, 1, 2, 1, 2];

  searchText: string = '';
  status: string = '';
  sort: number = 1;

  pageNo: number = 0;
  pagin!: number;
  pages!: number[];
  pettyCashLimit;
  responseMessage = '';
  uploadedFile;
  fileRequired: boolean = true;
  constructor(
    private pettyCashService: PettyCashService,
    public _subjectService: SubjectService,
    public headerService: HeaderService,
    private modalService: NgbModal,
    private router: Router,
    config: NgbModalConfig,
    private sharedService: SharedService,
    private _commonService: CommonService,
  ) {
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnInit(): void {
    // this.headerService.setTitle('Petty Cash > Petty Cash Request');

    this.GetRegisterPettyCashRequestLast();
    this.getRegistersPettyCashLimits();
  }
  fileAction(action) {
    if (action == 'view') {
      const modelRef = this.modalService.open(ViewFileComponent, {
        size: 'md',
      })
      modelRef.componentInstance.imageUploadedView = this.uploadedFile;
    }
    else {
      (document.getElementById('file-input') as HTMLInputElement).value = null;
      this.uploadedFile = null;
      this.fileRequired = false;
    }
  }
  onFileChange(event) {
    let files = [...event.target.files];
    if (files.length > 0) {
      let isInvalid = this._commonService.checkInvalidImageFormat(files);
      if (!isInvalid) {
        files.forEach((file: File) => {
          this._commonService.fileToBase64(file).then(response => {
            this.uploadedFile = response;
            this.fileRequired = true;
          })
        })
      }
    }
  }
  Viewdetails(item) {
    this.router.navigateByUrl('/petty-cash/request-details/1');
    this.headerService.setTitle(
      'Petty Cash > Petty Cash Replenishment Request > View Details'
    );
  }
  handleRecevingRequest() {
    if (!this.uploadedFile?.file && !this.LastRequest?.confirmationVoucherUploaded) {
      this.fileRequired = false;
      return;
    }
    let formData = new FormData();
    formData.append('PettyCashRequestId', this.LastRequest.pettyCashRequestId);
    formData.append('ConfirnReceivingVoucher', this.uploadedFile?.file || '');
    this.pettyCashService.confirmReceivePettyCashAmount(formData).subscribe(response => {
      console.log(response);
      if (response.data) {
        this.responseMessage = 'Successfully received!';
        this._subjectService.replenishmentRequestReceived.next(true);
        this.GetRegisterPettyCashRequestLast();
        this.uploadedFile = null;
        this.fileRequired = true;
        setTimeout(() => this.responseMessage = '', 5000)
      }
    }, error => {
      this.responseMessage = error?.error?.errors[0]?.ErrorMessageEn;
      setTimeout(() => this.responseMessage = '', 5000)
    })
  }
  viewExpense() {
    this.headerService.setTitle('petty-cash > View role');
  }abc
  getRegistersPettyCashLimits() {
    this.pettyCashService
      .GetRegistersPettyCashLimits(
        this.sharedService.getRegister?.registerObj?.id
      )
      .subscribe((response: any) => {
        if (response) {
          console.log(response);
          this.pettyCashLimit = response.data[0];
        }
      });
  }
  openModalAddREquest() {
    const modalRef = this.modalService.open(AddRequestComponent, {
      size: 'xl',
    });
    modalRef.componentInstance.type = 'add';
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      this.modalService.dismissAll();
      this.GetRegisterPettyCashRequestLast();
    });
  }

  LastRequest: any;
  GetRegisterPettyCashRequestLast() {
    return this.pettyCashService
      .GetRegisterPettyCashRequestLast()
      .subscribe((response: any) => {
        if (response) {
          this.LastRequest = response.data;
          this.GetRegisterPettycashrequest();
        }
      });
  }

  GetRegisterPettycashrequest() {
    return this.pettyCashService
      .GetRegisterPettycashrequest(
        this.searchText,
        this.sort,
        this.status,
        this.pageNo
      )
      .subscribe((response: any) => {
        if (response) {
          this.expenses = response.data.filter(x => x.pettyCashRequestId != this.LastRequest.pettyCashRequestId);
          this.totalRecords = response.info.totalRecordsCount;
          this.pagin = Math.ceil(this.totalRecords / 6);
          this.pages = _.range(this.pagin);
          //   this.pager.EventsCount = response["TotalCount"];
          // this.pager.setPage(this.pager.GlobalPageIndex, false);
        }
      });
  }
  searchRoles(event: any) {
    console.log(event?.target.value);
    const text = event.target.value;
    if (text.length >= 3) {
      this.searchText = text;
      this.pageNo = 0;
      this.GetRegisterPettycashrequest();
    }
    if (text.length == 0) {
      this.GetRegisterPettycashrequest();
    }
  }
  removeSearch() {
    this.searchText = '';
    this.sort = 1;
    this.GetRegisterPettycashrequest();
  }

  sortByBranch() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.GetRegisterPettycashrequest();
  }
  sortByRegisterNo() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.GetRegisterPettycashrequest();
  }
  sortByRegisterName() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.GetRegisterPettycashrequest();
  }

  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.GetRegisterPettycashrequest();
  }

  sortByStatus() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.GetRegisterPettycashrequest();
  }
  setPage(page: number) {
    this.pageNo = page;
    this.GetRegisterPettycashrequest();
    window.scroll(0, 0);
  }

  openMdalImage(item) {
    const modalRef = this.modalService.open(ModalImageComponent);
    modalRef.componentInstance.expenseDetails = item;
  }

  openModalExpenseView(item) {
    const modalRef = this.modalService.open(AddRequestComponent, {
      size: 'xl',
    });
    modalRef.componentInstance.type = 'view';
    modalRef.componentInstance.pettyCashRequestId = item.pettyCashRequestId;
  }
}
