import { HttpEventType } from '@angular/common/http';
import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  ViewChild,
} from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../services/common.service';
import { ModalMessageComponent } from '../../../../../shared/components/modal-message/modal-message.component';
import { ExpenseConfirmComponent } from '../expense-confirm/expense-confirm.component';
import { ModalImageComponent } from '../modal-image/modal-image.component';
import { PettyCashService } from '../services/petty-cash.service';
import { ViewFileModalComponent } from '../view-file-modal/view-file-modal.component';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-add-expense',
  templateUrl: './add-expense.component.html',
  styleUrls: ['./add-expense.component.scss'],
})
export class AddExpenseComponent {
  formGroup: FormGroup;
  @Input() expenseId: number;
  errorMessage: any;
  submitted = false;
  isLoading: boolean;
  isExpenseUpdated: boolean;
  isShowToUplaod: boolean = true;
  type: string;
  isViewFile: boolean;
  isLoadingView: boolean = true;
  costCenterList = [];
  @ViewChild('fileUpload', { static: false })
  InputVar: ElementRef;
  @Output() sendtoLoadData = new EventEmitter();
  constructor(
    private pettyCashService: PettyCashService,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private modalService: NgbModal,
    public activeModal: NgbActiveModal,
    private _commonService: CommonService,
    private _datePipe: DatePipe
  ) { }

  ngOnInit(): void {
    setTimeout(() => {
      this.isLoadingView = false;
    }, 2000);

    this.getForm();
    this.getExpenseCategory();
    if (this.expenseId) {
      this.isShowToUplaod = false;
      this.GetExpenseDetails();
    }
    this.getCostsCenter();
  }

  getForm() {
    this.formGroup = this.fb.group({
      ExpenseRecordId: 0,
      InvoiceNo: [''],
      ExpenseCategoryId: null,
      CostCenterId: [null, [Validators.required]],
      InvoiceDate: [{ value: null, disabled: true }, [Validators.required]],
      approverName: [{ value: '', disabled: true }],
      ExpenseAmount: ['', [Validators.required, Validators.pattern('^[0-9]+(\.[0-9]{1,20})?$')]],
      //ExpenseTax: ['', [Validators.required, Validators.pattern('^[0-9]+(\.[0-9]{1,20})?$')]],
      ExpenseTax: ['0', [Validators.required]],
      ExpenseTotalAmount: [{ value: '', disabled: true }, [Validators.required]],
      ExpenseNotes: [''],
      // Status: 2001,
      Attachments: null,
    });
  }
  getCostsCenter() {
    this.pettyCashService.getCostCenters().subscribe((response: any) => {
      this.costCenterList = response.data;
    })
  }
  handleFocusOut(control) {
    if (this.isShowExpense) {
      let value = +this.formGroup.controls[control].value;
      if (value > 0) {
        this.formGroup.controls[control].setValue(value.toFixed(3));
        //let tax = ((+this.formGroup.value['ExpenseAmount']) / 100) * (+this.formGroup.value['ExpenseTax']);
        //this.formGroup.controls['ExpenseTotalAmount'].setValue((tax + (+this.formGroup.value['ExpenseAmount'])).toFixed(3));
        //this.formGroup.controls[control].setErrors(null);
        let tax = (((+this.formGroup.value['ExpenseTax']) / 100) + 1) * (+this.formGroup.value['ExpenseAmount']);
        this.formGroup.controls['ExpenseTotalAmount'].setValue(tax.toFixed(3));
        this.formGroup.controls[control].setErrors(null);
      }
      else {
        this.formGroup.controls[control].setErrors({ 'zeroAmount': true });
        this.formGroup.controls['ExpenseTotalAmount'].setValue('');
      }
    }
  }
  handleTotalAmountChange() {
    let value = +this.formGroup.get('ExpenseTotalAmount').value;
    if (value > 0) {
      //this.formGroup.get('ExpenseTotalAmount').setValue(value.toFixed(3));
      //let tax = ((+this.formGroup.value['ExpenseTotalAmount']) / 100) * (+this.formGroup.value['ExpenseTax']);
      //this.formGroup.controls['ExpenseAmount'].setValue(((+this.formGroup.value['ExpenseTotalAmount']) - tax).toFixed(3));
      let amount = (+this.formGroup.value['ExpenseTotalAmount']) / (((+this.formGroup.value['ExpenseTax']) / 100) + 1);
      this.formGroup.controls['ExpenseAmount'].setValue(amount.toFixed(3));

    }
  }
  get formValid(): { [key: string]: AbstractControl } {
    return this.formGroup.controls;
  }

  onSubmit() {
    // console.log(this.formGroup.value);
    if (!this.formGroup.valid) {
      this.formGroup.markAllAsTouched();
      return;
    }
    if (this.formGroup.get('ExpenseRecordId')?.value == 0) {
      this.AddExpense();
    } else {
      this.EditExpense();
    }
  }

  AddExpense() {
    this.isLoading = true;
    this.submitted = true;

    if (
      this.formGroup.invalid ||
      this.formGroup.get('InvoiceNo')?.value <= 0 ||
      this.formGroup.get('ExpenseTotalAmount')?.getRawValue() <= 0 ||
      this.formGroup.get('Attachments').value == null
    ) {
      this.isLoading = false;
      return window.scroll(0, 0);
    } else {
      const formData = new FormData();
      formData.append('InvoiceNo', this.formGroup.get('InvoiceNo')?.value);
      formData.append('ExpenseCategoryId', this.formGroup.get('ExpenseCategoryId')?.value);
      formData.append('CostCenterId', this.formGroup.get('CostCenterId')?.value);
      formData.append('ExpenseAmount', this.formGroup.get('ExpenseAmount')?.value);
      formData.append('ExpenseTotalAmount', this.formGroup.get('ExpenseTotalAmount')?.getRawValue());
      formData.append('ExpenseTax', this.formGroup.get('ExpenseTax')?.value);
      formData.append('ExpenseNotes', this.formGroup.get('ExpenseNotes')?.value);
      formData.append('Attachments', this.formGroup.get('Attachments')?.value);
      return this.pettyCashService.AddPettyCashExpense(formData).subscribe(
        (event: any) => {
          if (event.type === HttpEventType.UploadProgress) {
            this.progress = Math.round((event.loaded / event.total) * 100);
          } else if (event.type === HttpEventType.Response) {
            setTimeout(() => {
              this.progress = 0;
            }, 1500);
            if (event.body.isSuccess == true) {
              this.isLoading = false;
              const modalRef = this.modalService.open(ModalMessageComponent);
              modalRef.componentInstance.type = 'success';
              modalRef.componentInstance.message =
                'Your expense is added successfully';
              modalRef.componentInstance.routeName = '/petty-cash';
              modalRef.componentInstance.name = 'add';
              setTimeout(() => {
                this.sendtoLoadData.emit();
              }, 2000);
            } else {
              this.isLoading = false;
              const modalRef = this.modalService.open(ModalMessageComponent);
              modalRef.componentInstance.type = 'error';
              modalRef.componentInstance.messageError =
                event.body.errors || event.body.Errors;
              this.errorMessage = event.body.Errors || event.body.errors;
            }
          }
        }, (error) => {
          const modalRef = this.modalService.open(ModalMessageComponent);
          modalRef.componentInstance.type = 'error';
          modalRef.componentInstance.messageError = error?.error?.errors;
          this.isLoading = false;
        });
    }
  }

  EditExpense() {
    this.isLoading = true;

    if (this.formGroup.invalid) {
      console.log('invalid amount');
      this.isLoading = false;
      return window.scroll(0, 0);
    } else {
      const formData = new FormData();
      let invoiceDate = this._datePipe.transform(this.formGroup.get('InvoiceDate')?.value, 'yyyy-MM-dd')
      formData.append('ExpenseRecordId', this.formGroup.get('ExpenseRecordId')?.value);
      formData.append('CostCenterId', this.formGroup.get('CostCenterId')?.value);
      formData.append('InvoiceNo', this.formGroup.get('InvoiceNo')?.value);
      formData.append('InvoiceDate', invoiceDate);
      formData.append('ExpenseCategoryId', this.formGroup.get('ExpenseCategoryId')?.value);
      formData.append('ExpenseAmount', this.formGroup.get('ExpenseAmount')?.value);
      formData.append('ExpenseTotalAmount', this.formGroup.get('ExpenseTotalAmount')?.getRawValue());
      formData.append('ExpenseTax', this.formGroup.get('ExpenseTax')?.value);
      formData.append('ExpenseNotes', this.formGroup.get('ExpenseNotes')?.value);
      formData.append('Attachments', this.formGroup.get('Attachments')?.value);
      this.pettyCashService.EditPettyCashExpense(formData).subscribe((event: any) => {
        //if (event.type === HttpEventType.UploadProgress) {
        //  this.progress = Math.round((event.loaded / event.total) * 100);
        //}
        //else if (event.type === HttpEventType.Response) {
        //setTimeout(() => {this.progress = 0;}, 1500);
        if (event.data) {
          this.isLoading = false;
          this.isExpenseUpdated = true;
          const modalRef = this.modalService.open(ModalMessageComponent);
          modalRef.componentInstance.type = 'success';
          modalRef.componentInstance.message = 'Your expense is updated successfully';
          modalRef.componentInstance.routeName = '/petty-cash';
          modalRef.componentInstance.name = 'add';
          modalRef.componentInstance.dismissOnlyActive = true;
          this.sendtoLoadData.emit({ status: this.expenseDetails.status })
        }
        else {
          this.isLoading = false;
          const modalRef = this.modalService.open(ModalMessageComponent);
          modalRef.componentInstance.type = 'error';
          modalRef.componentInstance.messageError = event?.error?.errors;
          this.errorMessage = event.Errors || event.errors;
        }
        //}
      }, (error) => {
        const modalRef = this.modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'error';
        modalRef.componentInstance.messageError = error?.error?.errors;
        this.isLoading = false;
      });
    }
  }
  expenseRevised() {
    const formData = new FormData();
    formData.append('expenseRecordId', this.expenseDetails.expenseRecordId);
    this.pettyCashService.expenseRevised(formData).subscribe(response => {
      if (response.data) {
        this.sendtoLoadData.emit({ expenseRevised: true });
      }
    })
  }
  isPending: boolean;
  expenseDetails: any;
  GetExpenseDetails() {
    this.formGroup.reset();
    this.pettyCashService
      .GetPettyCashExpenseDetails(this.expenseId)
      .subscribe((response: any) => {
        if (response) {
          this.expenseDetails = response.data;
          if (this.expenseDetails.status == 2003 || this.expenseDetails.status == 2011 || this.expenseDetails.status == 2012) {
            this.isPending = true;
          }
          console.log(this.expenseDetails);
          this.formGroup.get('ExpenseRecordId').patchValue(this.expenseDetails.expenseRecordId);
          this.formGroup.get('ExpenseAmount').patchValue(this.expenseDetails.expenseAmount);
          this.formGroup.get('ExpenseTotalAmount').patchValue(this.expenseDetails.expenseTotalAmount);
          this.formGroup.get('InvoiceNo').patchValue(this.expenseDetails.invoiceNo);
          this.formGroup.get('InvoiceDate').patchValue((this.expenseDetails?.invoiceDate) && new Date(this.expenseDetails?.invoiceDate) || null);
          this.formGroup.get('ExpenseCategoryId').patchValue(this.expenseDetails.expenseCategoryId);
          this.formGroup.get('CostCenterId').patchValue(this.expenseDetails.costCenter.costCenterId);
          this.formGroup.get('approverName').patchValue(this.expenseDetails.costCenter?.approverUser?.fullName);
          this.formGroup.get('ExpenseTax').patchValue(this.expenseDetails.expenseTax);
          this.formGroup.get('ExpenseNotes').patchValue(this.expenseDetails.expenseNotes == 'null' ? '' : this.expenseDetails.expenseNotes);
          this.formGroup.get('Attachments').patchValue(this.expenseDetails.Attachments);
          this.formGroup.controls['ExpenseCategoryId'].disable();
          this.formGroup.controls['CostCenterId'].disable();
          this.formGroup.controls['ExpenseTax'].disable();
          this.formGroup.controls['ExpenseTotalAmount'].disable();
        }
      });
  }

  expenseCategory: any;
  getExpenseCategory() {
    this.pettyCashService.getLookupsById(12).subscribe(
      (response: any) => {
        // this.expenseCategory = response.data;
        this.expenseCategory = response.data.map((x) => ({
          id: x.id,
          name: x.name[0].lookupName,
        }));
      },
      (error) => {
        this.errorMessage = error.message;
      }
    );
  }

  enter: boolean;
  myFiles: string[] = [];
  urls: any[] = [];
  imageError: string;
  progress: number = 0;
  filePath;
  fileName: string;
  errorMsgUplaod: string;
  filesDropped(files: any) {
    this.enter = false;
    if (
      files[0].file.type == 'image/jpeg' ||
      files[0].file.type == 'image/jpg' ||
      files[0].file.type == 'image/png' ||
      files[0].file.type == 'application/pdf'
    ) {
      if (files && files[0]) {
        this._commonService.fileToBase64(files[0]?.file).then(response => {
          this.filePath = response;
        })
      }

      this.fileName = files[0].file.name;
      this.errorMsgUplaod = '';
      this.formGroup.get('Attachments')?.patchValue(files[0].file);
    } else {
      this.errorMsgUplaod =
        'This file not support , Supported formates: JPEG, JPG, PNG, PDF';
    }
  }
  removeImageName() {
    this.fileName = '';
    this.filePath = '';
    this.errorMsgUplaod = '';
    this.InputVar.nativeElement.value = '';
    this.formGroup.get('Attachments')?.patchValue(null);
  }
  onSelectFile(event: any) {
    if (event.target.files && event.target.files[0]) {
      this._commonService.fileToBase64(event.target.files[0]).then(response => {
        this.filePath = response;
      })
    }
    this.fileName = event.target.files[0].name;
    this.errorMsgUplaod = '';
    this.formGroup.get('Attachments')?.patchValue(event.target.files[0]);
  }

  removeImage(i) {
    this.urls.splice(i, 1);
    this.myFiles.splice(i, 1);
  }
  removeImg() {
    this.errorMsgUplaod = '';
    this.isShowToUplaod = !this.isShowToUplaod;
  }

  openMdalImage() {
    const modalRef = this.modalService.open(ViewFileModalComponent);
    modalRef.componentInstance.imageUploadedView = this.filePath;
  }

  openMdalImageView() {
    const modalRef = this.modalService.open(ModalImageComponent);
    modalRef.componentInstance.imageUploadedView =
      this.expenseDetails?.files[0]?.attachmentPath;
  }

  numberOnly(event): boolean {
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  isShowExpense: boolean = false;
  editExpenseInfo() {
    // this.isShowBranchInfo = this.isShowBranchInfo == false ? true : false;

    if (this.isShowExpense == false) {
      this.isShowExpense = true;
      this.formGroup.controls['ExpenseCategoryId'].enable();
      this.formGroup.controls['CostCenterId'].enable();
      this.formGroup.controls['ExpenseTax'].enable();
      this.formGroup.controls['ExpenseTotalAmount'].enable();
      this.formGroup.controls['InvoiceDate'].enable();
    } else if (this.isShowExpense == true) {
      this.formGroup.controls['InvoiceDate'].disable();
      this.formGroup.controls['ExpenseCategoryId'].disable();
      this.formGroup.controls['CostCenterId'].disable();
      this.formGroup.controls['ExpenseTax'].disable();
      this.formGroup.controls['ExpenseTotalAmount'].disable();
      //const modalRef = this.modalService.open(ExpenseConfirmComponent);
      //modalRef.componentInstance.name = 'edit';
      //modalRef.componentInstance.semdToConfirm.subscribe((result: any) => {
      //  console.log('result', result);
      //  this.isShowExpense = false;
      //  this.modalService.dismissAll();
      //  this.onSubmit();
      //});
      //modalRef.componentInstance.sendToClose.subscribe((result: any) => {
      //  console.log('resulttoclose', result);
      //  this.isShowExpense = false;
      //});
    }
  }
}
