import { Component, ElementRef, EventEmitter, Output, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PettyCashService } from '../services/petty-cash.service';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../services/common.service';
import { ViewFileComponent } from '../../collect/payment-modal/view-file/view-file.component';
import { ModalMessageComponent } from '../../../../../shared/components/modal-message/modal-message.component';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-add-expense-v2',
  templateUrl: './add-expense-v2.component.html',
  styleUrls: ['./add-expense-v2.component.scss']
})
export class AddExpenseV2Component {
  paymentDetails;
  expenseForm: FormGroup;
  expenseCategoryList = [];
  costCenterList = [];
  uploadedFile;
  isPending = false;
  @Output() eventData = new EventEmitter();
  @ViewChild('fileInput') inputRef: ElementRef<any>;
  constructor(
    private fb: FormBuilder,
    private pettyCashService: PettyCashService,
    private _commonService: CommonService,
    private _modalService: NgbModal,
    public _activeModel: NgbActiveModal,
    public _datePipe: DatePipe,
  ) { }
  ngOnInit() {
    this.initForm();
    this.getExpenseCategory();
    this.getCostsCenter();
  }
  initForm() {
    this.expenseForm = this.fb.group({
      invoiceNo: ['', [Validators.required]],
      invoiceDate: [null, [Validators.required]],
      expenseCategoryId: [this.paymentDetails.categoryId, [Validators.required]],
      costCenterId: [this.paymentDetails.costCenter.costCenterId, [Validators.required]],
      approverName: [{ value: this.paymentDetails?.costCenter?.approverUser?.fullName, disabled: true }],
      pettyCashPaymentId: [this.paymentDetails.pettyCashPaymentId],
      expenseTotalAmount: [this.paymentDetails.expenseTotalAmount || '', [Validators.required]],
      expenseAmount: [this.paymentDetails.amount || '', [Validators.required]],
      expenseTax: [0],
      expenseNotes: [this.paymentDetails.notes],
      attachment: ['', [Validators.required]],
    })
  }
  addExpense() {
    if (this.expenseForm.invalid) {
      this.expenseForm.markAllAsTouched();
      return;
    }
    this.isPending = true;
    let formData = new FormData();
    let values = this.expenseForm.value;
    formData.append('InvoiceNo', values?.invoiceNo);
    let invoiceDate = this._datePipe.transform(values?.invoiceDate, 'yyyy-MM-dd')
    formData.append('invoiceDate', invoiceDate);
    formData.append('ExpenseCategoryId', values?.expenseCategoryId);
    formData.append('PettyCashPaymentId', values?.pettyCashPaymentId);
    formData.append('ExpenseTotalAmount', this.expenseForm.get('expenseTotalAmount').value)
    formData.append('ExpenseAmount', values?.expenseAmount);
    formData.append('ExpenseTax', values?.expenseTax);
    formData.append('ExpenseNotes', values?.expenseNotes);
    formData.append('CostCenterId', values?.costCenterId);
    formData.append('Attachments', this.uploadedFile.file);
    this.pettyCashService.addExpenseV2(formData).subscribe(response => {
      if (response.isSuccess) {
        this.expenseForm.reset();
        const modalRef = this._modalService.open(ModalMessageComponent);
        modalRef.componentInstance.type = 'success';
        modalRef.componentInstance.message = 'Payment Successfully Added';
        this.eventData.emit(true);
      }
    }, error => {
      const modalRef = this._modalService.open(ModalMessageComponent);
      modalRef.componentInstance.type = 'error';
      modalRef.componentInstance.messageError = error?.error?.errors;
    }).add(() => this.isPending = false);
  }
  fileAction(action) {
    if (action == 'view') {
      const modelRef = this._modalService.open(ViewFileComponent, {
        size: 'md',
      })
      modelRef.componentInstance.imageUploadedView = this.uploadedFile;
    }
    else {
      (document.getElementById('file-input') as HTMLInputElement).value = null;
      this.expenseForm.get('attachment')?.setValue('');
      this.uploadedFile = null;
    }
  }
  onFileChange(event) {
    let files = [...event.target.files];
    if (files.length > 0) {
      let isInvalid = this._commonService.checkInvalidImageFormat(files);
      if (!isInvalid) {
        files.forEach((file: File) => {
          this._commonService.fileToBase64(file).then(response => {
            this.uploadedFile = response;
            this.expenseForm.get('attachment')?.setValue('Attachment');
          })
        })
      }
    }
  }
  handleFocusOut(control) {
    let value = +this.expenseForm.controls[control].value;
    if (value > 0) {
      this.expenseForm.controls[control].setValue(value.toFixed(3));
      //let tax = ((+this.expenseForm.value['expenseAmount']) / 100) * (+this.expenseForm.value['expenseTax']);
      //this.expenseForm.controls['expenseTotalAmount'].setValue((tax + (+this.expenseForm.value['expenseAmount'])).toFixed(3));

      let tax = (((+this.expenseForm.value['expenseTax']) / 100) + 1) * (+this.expenseForm.value['expenseAmount']);
      this.expenseForm.controls['expenseTotalAmount'].setValue(tax.toFixed(3));
    }
  }
  handleTotalAmountChange() {
    let value = +this.expenseForm.get('expenseTotalAmount').value;
    if (value > 0) {
      //  this.expenseForm.get('expenseTotalAmount').setValue(value.toFixed(3));
      //  let tax = ((+this.expenseForm.value['expenseTotalAmount']) / 100) * (+this.expenseForm.value['expenseTax']);
      //  this.expenseForm.controls['expenseAmount'].setValue(((+this.expenseForm.value['expenseTotalAmount']) - tax).toFixed(3));
      let amount = (+this.expenseForm.value['expenseTotalAmount']) / (((+this.expenseForm.value['expenseTax']) / 100) + 1);
      this.expenseForm.controls['expenseAmount'].setValue(amount.toFixed(3));

    }
  }
  costCenterChange(event) {
    this.expenseForm.get('approverName').setValue(event?.approverUser?.fullName)
  }
  getExpenseCategory() {
    this.pettyCashService.getLookupsById(12).subscribe((response: any) => {
      //this.expenseCategoryList = response.data.map((x) => );
      this.expenseCategoryList = response.data.map((x) => {
        return {
          id: x.id,
          name: x.name[0].lookupName
        }
      })
    });
  }
  getCostsCenter() {
    this.pettyCashService.getCostCenters().subscribe((response: any) => {
      this.costCenterList = response.data;
    })
  }

}
