import { Component, Input, Sanitizer } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-view-file-modal',
  templateUrl: './view-file-modal.component.html',
  styleUrls: ['./view-file-modal.component.scss']
})
export class ViewFileModalComponent {
  @Input() expenseDetails: any;
  @Input() imageUploadedView: any;
  isPdf: boolean;
  imagePath: string;
  constructor(
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private router: Router,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit(): void {
    console.log(this.imageUploadedView);
    //if (this.imageUploadedView !== 'application/pdf') {
    //  this.imagePath = this.imageUploadedView.base64;
    //}
  }
  viewPdf(file) {
    let src = URL.createObjectURL(file);
    return this.sanitizer.bypassSecurityTrustResourceUrl(src)
  }
}
